header {
  font-size: 1rem;
  border-bottom: 0px solid $primaryColor;
}

.headerContain {
  background-color: $white;
  padding-top: toRem(10);
  padding-bottom: toRem(10);
  padding-left: toRem(5);
  padding-right: toRem(5);

  .header-content {
    max-width: $max-width-full;
    display: flex;
    margin: 0 auto;
  }
}

.search-wrapper {
  flex-grow: 1;
  justify-content: center;
  position: relative;
  .search-bar {
    width: 90%;
    margin: 0px auto;
    position: relative;
    input {
      width: 100%;
      margin: 0px auto;
      border-radius: 15px 0px 0px 15px;
      border: 1px solid $borderColor;
      padding: 4px 10px 2px 10px;
      font-size: 0.85rem;
    }
  }
  .search-dropdown {
    visibility: hidden;
    position: absolute;
    background-color: $white;
    border: 1px solid;
    width: 100%;
    top: 100%;
    z-index: 20;
    border-left: 1px solid $backgroundColor;
    border-bottom: 1px solid $backgroundColor;
    border-right: 1px solid $backgroundColor;
    border-top: 0px;
    border-radius: 15px;
    padding: toRem(16);
    max-height: 70vh;
    overflow: auto;
  }
  .search-drop-active {
    visibility: visible;
  }
}

.is-search-icon {
  border-radius: 0px 15px 15px 0px;
  background-color: $primaryAccent;
}

#bbj_search:focus {
  outline: 0px;
}
.headerContain img {
  margin-top: 5px;
}

.headerLeft {
  display: flex;
  align-items: center;
  flex-grow: 1;
  border: 0px solid black;
}

.headerLogoFull {
  display: none;
  @include media(">tablet") {
    display: block;
  }
}

.headerLogoFull img {
  max-width: 350px;
}

.headerLogoMobile {
  display: block;
  @include media(">tablet") {
    display: none;
  }
}

.mobileMenu__icon,
.mobileMenu {
  border: 0px solid;
  display: block;
  font-size: 1.75rem;
  color: $primaryColor;
  position: static;
  float: right;

  @include media(">tablet") {
