.menuShow {
  display: none;
  background-color: rgba($primaryColor, 0.9);
  opacity: 0;
  position: absolute;
  transition: opacity 0.3s ease-out, visibility 0.3s ease-out, transform 0.3s ease-out;
  transform: translateY(0);
  width: 100%;
  z-index: 10;
}

.menuShow--active {
  opacity: 1;
  display: block;
  position: absolute;
  width: 100%;
  transform: translateY(0);
  transition: opacity 0.3s ease-out, visibility 0.3s ease-out, transform 0.3s ease-out;
}

