#update-box {
  border: 1px solid rgba($primaryColor, 0.2);
  background-color: #fff;
  padding: 10px;
  width: 100%;
  position: fixed;
  margin: 0px auto;
  display: block;
  bottom: 0%;
  left: 50%;
  transform: translate(-50%, 0%);
  z-index: 50;

  @include media(">tablet") {
    max-width: 1000px;
  }

  .bbj-nl-first,
  .bbj-nl-last,
  .bbj-nl-email {
    margin: 0px;
  }
}

.update-box-body {
  height: 0px;
  overflow: auto;
  padding: 0px 5px;
}
.update-toggle {
  height: 40vh;
}
.update-box-header {
  padding: 5px;
  background-color: $primaryColor;
  position: relative;
  color: #ffffff;
  font-size: toRem(16);
  font-weight: 600;
}
.update-box-button {
  position: absolute;
  right: 0;
  top: 0;
  padding: 3px 10px 3px 0px;
  font-size: 20px;
}

.newsletter-bar {
  background-color: rgba($primaryColor, 0.9);
  border-radius: 10px;
  padding: 5px 20px 15px 20px;
  margin: 20px auto;
  width: 95%;
  position: relative;

  h4 {
    text-align: center;
    color: #ffffff;
  }

  .nl-icon {
    position: absolute;
    top: -15px;
    left: -15px;
    border-radius: 50%;
    padding: 5px;
    background: rgb(53, 84, 110);
    background: linear-gradient(0deg, rgba(53, 84, 110, 1) 0%, rgba(144, 175, 201, 1) 100%);

    color: #fff;
    height: 34px;
    width: 34px;
    .nl-icon-interior {
      font-size: toRem(16);
      font-weight: 400;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 1px solid #ffffff;
      border-radius: 50%;
      height: 28px;
      width: 28px;
      text-align: center;

      .fa-regular {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

